import "./App.css";
import logo from "./logo.svg";
import { Navbar, Container, Nav, NavDropdown, Card } from "react-bootstrap";
import React, { useState } from "react";
import Data from "./data.js";
import axios from "axios";
import Detail from "./Detail.js";
import { mapToStyles } from "@popperjs/core/lib/modifiers/computeStyles";

import { Link, Route, Switch } from "react-router-dom";

function ProductUI(props) {
  var returnCode = [];
  var product = props.shoes;
  for (var i = 0; i < props.shoes.length; i++) {
    returnCode.push(
      <div className="col-md-4">
        <img
          src={"https://codingapple1.github.io/shop/shoes" + (i + 1) + ".jpg"}
          width="100%"
        ></img>
        <h4>{product[i].title}</h4>
        <p>{product[i].content}</p>
        <h4>{product[i].price}</h4>
      </div>
    );
  }
  return returnCode;
}

function App() {
  let [shoes, shoesChange] = useState(Data);
  let [loading, loadingChange] = useState(false);
  let [stock, stockChange] = useState([10, 11, 12]);
  return (
    <div className="App">
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="#home">hani</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link as={Link}>
              <Link to="/">home</Link>
            </Nav.Link>
            <Nav.Link as={Link}>
              <Link to="/detail">detail</Link>
            </Nav.Link>
            <Nav.Link as={Link} href="#pricing">
              Pricing
            </Nav.Link>
          </Nav>
        </Container>
      </Navbar>
      <Switch>
        <Route exact path="/">
          <Card style={{ width: "100%" }}>
            <Card.Body>
              <Card.Title>Card Title</Card.Title>
              <Card.Subtitle className="mb-2 text-muted">
                점보박스 대타
              </Card.Subtitle>
              <Card.Text>
                Some quick example text to build on the card title and make up
                the bulk of the card's content.
              </Card.Text>
              <Card.Link href="#">Card Link</Card.Link>
              <Card.Link href="#">Another Link</Card.Link>
            </Card.Body>
          </Card>
          <div className="container">
            <div className="row">
              <ProductUI shoes={shoes} />
            </div>
            {loading === true ? (
              <div className="my-alert">
                <p>로딩중 몰?루</p>
              </div>
            ) : null}
            <button
              className="btn btn-primary"
              onClick={() => {
                loadingChange(true);

                axios
                  .get("https://codingapple1.github.io/shop/data2.json") //get요청
                  .then((result) => {
                    loadingChange(false);
                    shoesChange([...shoes, ...result.data]);
                  }) //성공시
                  .catch(); //실패시
              }}
            >
              더보기
            </button>
          </div>
        </Route>

        <Route path="/detail/:id">
          <Detail shoes={shoes} stock={stock} stockChange={stockChange} />
          <button className="btn btn-primary">더보기</button>
        </Route>
        {/* 콜론뒤에 아무 파라미터나 입력 */}
        <Route path="/:id">
          <div>이무거나 적어씅ㄹ대 보여주면됨</div>
        </Route>
      </Switch>

      {/* Switch : 일치되는거 하나만 보여줌 */}
    </div>
  );
}

export default App;
