import React, { useState, useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";
import styled from "styled-components";
import "./Detail.css";

let Box = styled.div`
  padding: 20px;
`;
let TitleBox = styled.div`
  font-size: 25px;
`;

function Info(props) {
  return <div>stock : {props.stock[props.id]}</div>;
}

function Detail(props) {
  let [alert, alertChange] = useState(true);
  let history = useHistory();
  let [inputdata, inputdataChange] = useState("");
  let { id } = useParams(); // url의 파라미터
  let productFind = props.shoes.find(function (product) {
    return product.id == id;
  });

  useEffect(() => {
    setTimeout(() => {
      alertChange(false);
    }, 2000);
  }, [alert, id]); //alert와 id가 변경될때만적용

  // 방문기록이 담겨있는 오브젝트
  return (
    <div className="container">
      <Box>
        <TitleBox className="red">detail</TitleBox>
      </Box>
      {alert === true ? (
        <div className="my-alert">
          <p>재고가 얼마 남지 않았습니다!</p>
        </div>
      ) : null}
      <input
        onChange={(e) => {
          inputdataChange(e.target.value);
        }}
      />
      <div> {inputdata}</div>
      <div className="row">
        <div className="col-md-6">
          <img
            src={
              "https://codingapple1.github.io/shop/shoes" +
              (productFind.id + 1) +
              ".jpg"
            }
            width="100%"
          />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{productFind.title}</h4>
          <p>{productFind.content}</p>
          <p>{productFind.price}원</p>
          <Info stock={props.stock} id={id} />
          <button className="btn btn-danger">주문하기</button>
          &nbsp;
          <button
            className="btn btn-danger"
            onClick={() => {
              history.goBack();
            }}
          >
            뒤로가기
          </button>
          &nbsp;
          <button
            className="btn btn-danger"
            onClick={() => {
              history.push("/");
            }}
          >
            home
          </button>
        </div>
      </div>
    </div>
  );
}

export default Detail;
